  padding-left: 0;
}

.program-container .root ul li {
  margin-left: 20px;
  list-style: none;
}

.root > ul > li {
  margin-left: 0px !important;
}

.program-container .add-item-title, .program-container .add-item-lesson-count {
  outline: none;
  border: none;
}

.program-container .show-on-hover > span {
  padding-right: 8px;
}

.program-container textarea {
  resize: none;
  vertical-align: bottom;
  max-width: 90%;
  display: inline-block;
}

.program-container input, .program-container input:hover, .program-container textarea, .program-container textarea:hover {
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
}

.program-container .nearer {
  margin-left: -5px;
}

.program-container .no-wrap {
  white-space: nowrap;
}

.search-icon {
  float: right;
  cursor: pointer;
}

body.modal-open {
  overflow: hidden;
  height: 100% !important;
}

body.modal-open .blur {
  -webkit-filter: blur(4px);
  overflow: hidden;
  height: 100%;
}

#searchModal.modal {
  overflow-y: hidden;
}

#searchModal {
  z-index: 100000;
}

#searchModal .modal-dialog {
  position: absolute;
  left: 25%;
  top: 25%;
  width: 50% !important;
  height: 50% !important;
  margin-top: 0px !important;
}

#searchModal .modal-content {
  padding: 10px 15px;
  height: 100% !important;
}

#searchModal .modal-content input, #searchModal .modal-content input:focus {
  border: none;
  border-bottom: 1px solid #eee;
  width: 100%;
  height: 34px;
  font-size: 18px;
  color: #707070;
  box-shadow: none;
  -webkit-box-shadow: none;
  outline: 0;
  margin-bottom: 15px;
}

#searchModal .modal-content input::-webkit-input-placeholder {
  color: #9d9d9d;
}

#searchModal .resultRow {
  margin: 0px;
  padding: 4px 2px;
  color: #9d9d9d;
}

#searchModal .active {
  background: #f5f5f5;
}

#searchModal .resultRow a {
  text-decoration: underline;
  font-size: 16px;
}

#searchResult {
  overflow-y: scroll;
  width: 100%;
  height: 80%;
}

.searchResultWraper {
  position: relative;
  height: 90%;
}

.searchResultWraper .notFound {
  position: absolute;
  left: 0px;
  top: 47%;
  font-size: 18px;
  text-align: center;
  color: #9d9d9d;
  width: 100%;
}

.hide {
  display: none !important;
}
